<script>
import TheSelectToken from '@/components/TheSelectToken.vue';
import TheInput from '@/components/TheInput.vue';
import TheInputWallet from '@/components/TheInputWallet.vue';

export default {
  name: 'the-order-popup',

  props: {
    onCloseOrderPopup: Function,
  },

  data() {
    return {
      sendTokenVal: {},
      walletLabel: '',
    };
  },

  methods: {
    onSendToken(val) {
      this.sendTokenVal = val;
    },
  },

  watch: {
    sendTokenVal() {
      if (this.sendTokenVal.code === 'BTC') {
        this.walletLabel = 'Your BTC wallet:';
      } else {
        this.walletLabel = 'YOUR ORI WALLET (ERC20 COMPATIBLE):';
      }
    },
  },

  components: {
    TheSelectToken,
    TheInput,
    TheInputWallet,
  },
};
</script>

