<script>
import TheOrderPopup from '@/components/TheOrderPopup.vue';
import PieTimer from '@/components/PieTimer.vue';

import TokenIcon from '@/components/TokenIcon.vue';

export default {
  name: "the-token-list",

  props: {
    type: {
      type: String,
      default: 'dcs',
    },
    searchToken: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      isShowOrderPopup: false,
      tokens: [
        {
          name: 'Ethereum',
          code: 'ETH',
          change: 2.2,
          value: '32423',
        },
        {
          name: 'Attention Token',
          code: 'BAT',
          change: -1.2,
          value: '8631',
        },
        {
          name: 'Blackcoin',
          code: 'BC',
          change: 5.2,
          value: '748',
        },
        {
          name: 'Dai Stablecoin',
          code: 'DAI',
          change: 1.2,
          value: '23323',
        },
        {
          name: 'Dash',
          code: 'DASH',
          change: -2,
          value: '3534',
        },
        {
          name: 'Decred',
          code: 'DCR',
          change: 1,
          value: '3534',
        },
        {
          name: 'EOS',
          code: 'EOS',
          change: 0.6,
          value: '766',
        },
        {
          name: 'Litecoin',
          code: 'LTC',
          change: 7.32,
          value: '3870',
        },
        {
          name: '0x',
          code: 'ZRX',
          change: 4.6,
          value: '2347238',
        },
      ]
    };
  },

  computed: {
    filteredTokens() {
      const searchToken = this.searchToken.toLowerCase();

      return searchToken
        ? this.tokens.filter(c => c.name.toLowerCase().includes(searchToken) || c.code.toLowerCase().includes(searchToken))
        : this.tokens;
    },
  },

  methods: {
    onOpenOrderPopup() {
      this.isShowOrderPopup = true;
    },
    onCloseOrderPopup() {
      this.isShowOrderPopup = false;
    },
  },

  components: {
    TheOrderPopup,
    PieTimer,
    TokenIcon,
  },
};
</script>

